@plugin "tailwindcss-animate";
@custom-variant dark (&:is(.dark *));
@tailwind base;
@tailwind components;
@tailwind utilities;
@theme inline {
  --radius-sm: calc(var(--radius) - 4px);
  --radius-md: calc(var(--radius) - 2px);
  --radius-lg: var(--radius);
  --radius-xl: calc(var(--radius) + 4px);
  --color-background: var(--background);
  --color-foreground: var(--foreground);
  --color-card: var(--card);
  --color-card-foreground: var(--card-foreground);
  --color-popover: var(--popover);
  --color-popover-foreground: var(--popover-foreground);
  --color-primary: var(--primary);
  --color-primary-foreground: var(--primary-foreground);
  --color-secondary: var(--secondary);
  --color-secondary-foreground: var(--secondary-foreground);
  --color-muted: var(--muted);
  --color-muted-foreground: var(--muted-foreground);
  --color-accent: var(--accent);
  --color-accent-foreground: var(--accent-foreground);
  --color-destructive: var(--destructive);
  --color-border: var(--border);
  --color-input: var(--input);
  --color-ring: var(--ring);
  --color-chart-1: var(--chart-1);
  --color-chart-2: var(--chart-2);
  --color-chart-3: var(--chart-3);
  --color-chart-4: var(--chart-4);
  --color-chart-5: var(--chart-5);
  --color-sidebar: var(--sidebar);
  --color-sidebar-foreground: var(--sidebar-foreground);
  --color-sidebar-primary: var(--sidebar-primary);
  --color-sidebar-primary-foreground: var(--sidebar-primary-foreground);
  --color-sidebar-accent: var(--sidebar-accent);
  --color-sidebar-accent-foreground: var(--sidebar-accent-foreground);
  --color-sidebar-border: var(--sidebar-border);
  --color-sidebar-ring: var(--sidebar-ring);
}
:root {
  --radius: 0.625rem;
  --background: oklch(1 0 0);
  --foreground: oklch(0.145 0 0);
  --card: oklch(1 0 0);
  --card-foreground: oklch(0.145 0 0);
  --popover: oklch(1 0 0);
  --popover-foreground: oklch(0.145 0 0);
  --primary: oklch(0.205 0 0);
  --primary-foreground: oklch(0.985 0 0);
  --secondary: oklch(0.97 0 0);
  --secondary-foreground: oklch(0.205 0 0);
  --muted: oklch(0.97 0 0);
  --muted-foreground: oklch(0.556 0 0);
  --accent: oklch(0.97 0 0);
  --accent-foreground: oklch(0.205 0 0);
  --destructive: oklch(0.577 0.245 27.325);
  --border: oklch(0.922 0 0);
  --input: oklch(0.922 0 0);
  --ring: oklch(0.708 0 0);
  --chart-1: oklch(0.646 0.222 41.116);
  --chart-2: oklch(0.6 0.118 184.704);
  --chart-3: oklch(0.398 0.07 227.392);
  --chart-4: oklch(0.828 0.189 84.429);
  --chart-5: oklch(0.769 0.188 70.08);
  --sidebar: oklch(0.985 0 0);
  --sidebar-foreground: oklch(0.145 0 0);
  --sidebar-primary: oklch(0.205 0 0);
  --sidebar-primary-foreground: oklch(0.985 0 0);
  --sidebar-accent: oklch(0.97 0 0);
  --sidebar-accent-foreground: oklch(0.205 0 0);
  --sidebar-border: oklch(0.922 0 0);
  --sidebar-ring: oklch(0.708 0 0);
}

.dark {
  --background: oklch(0.145 0 0);
  --foreground: oklch(0.985 0 0);
  --card: oklch(0.205 0 0);
  --card-foreground: oklch(0.985 0 0);
  --popover: oklch(0.205 0 0);
  --popover-foreground: oklch(0.985 0 0);
  --primary: oklch(0.922 0 0);
  --primary-foreground: oklch(0.205 0 0);
  --secondary: oklch(0.269 0 0);
  --secondary-foreground: oklch(0.985 0 0);
  --muted: oklch(0.269 0 0);
  --muted-foreground: oklch(0.708 0 0);
  --accent: oklch(0.269 0 0);
  --accent-foreground: oklch(0.985 0 0);
  --destructive: oklch(0.704 0.191 22.216);
  --border: oklch(1 0 0 / 10%);
  --input: oklch(1 0 0 / 15%);
  --ring: oklch(0.556 0 0);
  --chart-1: oklch(0.488 0.243 264.376);
  --chart-2: oklch(0.696 0.17 162.48);
  --chart-3: oklch(0.769 0.188 70.08);
  --chart-4: oklch(0.627 0.265 303.9);
  --chart-5: oklch(0.645 0.246 16.439);
  --sidebar: oklch(0.205 0 0);
  --sidebar-foreground: oklch(0.985 0 0);
  --sidebar-primary: oklch(0.488 0.243 264.376);
  --sidebar-primary-foreground: oklch(0.985 0 0);
  --sidebar-accent: oklch(0.269 0 0);
  --sidebar-accent-foreground: oklch(0.985 0 0);
  --sidebar-border: oklch(1 0 0 / 10%);
  --sidebar-ring: oklch(0.556 0 0);
}

@layer base {
  * {
    @apply border-border outline-ring/50;
  }
  body {
    @apply bg-background text-foreground;
  }
}
@layer components {
  .woocommerce-js input[type=number] {
    max-width: 100% !important;
  }
  button {
    background-color: transparent !important;
    border-color: var(--ast-border-color) !important;
    transition: border 0.2s;
  }
  button:hover, button:focus, button[data-highlighted] {
    background-color: transparent !important;
    border-color: var(--ast-global-color-0) !important;
  }
  .previous-button {
    background-color: #f5f5f5 !important;
    border: 1px solid var(--ast-border-color) !important;
  }
  .previous-button:hover, .previous-button:focus, .previous-button[data-highlighted] {
    background-color: var(--ast-global-color-0) !important;
    border: 1px solid var(--ast-global-color-0) !important;
  }
  .next-button {
    background-color: var(--ast-global-color-0) !important;
    border: 1px solid var(--ast-global-color-0) !important;
  }
  .next-button:hover, .next-button:focus, .next-button[data-highlighted] {
    background-color: #9dd2a2 !important;
    border: 1px solid #9dd2a2 !important;
  }
  .reset-button {
    background-color: #fecaca !important;
    border: 1px solid #fecaca !important;
  }
  .reset-button:hover, .reset-button:focus, .reset-button[data-highlighted] {
    color: white !important;
    opacity: 0.5 !important;
    background-color: var(--destructive) !important;
    border: 1px solid var(--destructive) !important;
  }
  .summary-edit-button {
    background-color: #f5f5f5 !important;
    border: 1px solid var(--ast-border-color) !important;
  }
  .summary-edit-button:hover, .summary-edit-button:focus, .summary-edit-button[data-highlighted] {
    background-color: var(--ast-global-color-0) !important;
    border: 1px solid var(--ast-global-color-0) !important;
  }
  .summary-subheading {
    margin-bottom: 1rem;
  }
  .summary-subheading p {
    margin-bottom: 0 !important;
  }
  .select-trigger:hover, .select-trigger:focus {
    border-width: 2px !important;
  }
  .tooltip-content {
    padding: 1.5rem !important;
    border-radius: calc(var(--radius) * 1.5);
    text-wrap: break-word;
    overflow-wrap: break-word;
    max-width: 300px;
  }
  [data-slot=tooltip-content] span svg {
    display: none !important;
  }
  .tooltip-trigger {
    margin-left: 6px;
    font-weight: 600;
    font-size: 16px;
    border-radius: 100%;
    border: 1px solid var(--ast-border-color);
    background-color: var(--ast-border-color);
    width: 16px;
    height: 16px;
    display: flex;
    align-items: center;
    justify-content: center;
    cursor: pointer;
  }
  .step-indicator {
    margin-bottom: 2rem;
    background: #f5f5f5;
    padding: 1.5rem;
    border-radius: calc(var(--radius) * 1.5);
  }
  .step-indicator .w-10 {
    width: 2.5rem !important;
    height: 2.5rem !important;
  }
  .step-indicator .text-xs {
    margin-top: auto !important;
    padding-top: 0.5rem !important;
    word-wrap: normal;
    overflow-wrap: normal;
    max-width: 100% !important;
  }
  .step-indicator .mx-2 {
    margin-left: 0.375rem !important;
    margin-right: 0.375rem !important;
  }
  @media (max-width: 1024px) {
    .step-indicator .w-10 {
      width: 2rem !important;
      height: 2rem !important;
      font-size: 0.8rem !important;
    }
    .step-indicator .mx-2 {
      margin-left: 0.25rem !important;
      margin-right: 0.25rem !important;
    }
  }
  @media (max-width: 640px) {
    .step-indicator {
      margin-bottom: 1.5rem;
      padding: 1rem;
    }
    .step-indicator .w-10 {
      width: 1.75rem !important;
      height: 1.75rem !important;
      font-size: 0.7rem !important;
    }
    .step-indicator .text-xs {
      display: none !important;
    }
    .step-indicator .mx-2 {
      margin-left: 0.15rem !important;
      margin-right: 0.15rem !important;
    }
    .step-indicator .mt-5 {
      margin-top: 0.875rem !important;
    }
    .step-indicator .mt-2 {
      margin-top: 0.25rem !important;
    }
  }
  .onea-multiple-step-form {
    max-width: 100%;
    width: 100% !important;
    overflow-x: hidden;
    padding: 2rem;
  }
  @media (max-width: 640px) {
    .onea-multiple-step-form {
      padding: 1rem;
    }
  }
  .onea-multiple-step-form .custom-error {
    padding: 1rem;
    margin-bottom: 1.5rem;
  }
  .onea-multiple-step-form .custom-error p {
    margin-bottom: 0 !important;
  }
  .onea-multiple-step-form .consumption-unit {
    margin-left: 0.15rem !important;
  }
  .onea-multiple-step-form .summary-field-wrapper {
    margin-top: 0.5rem !important;
    margin-bottom: 0.5rem !important;
  }
  .onea-multiple-step-form .summary-field-wrapper dd {
    margin-top: 0.25rem !important;
    margin-bottom: 0.25rem !important;
    margin-left: 0 !important;
    margin-right: 0 !important;
  }
  .onea-multiple-step-form .field-group {
    background: #f5f5f5 !important;
    border: 1px solid hsl(var(--border));
    border-radius: calc(var(--radius) * 1.5);
    padding: 1.5rem;
    margin-bottom: 1.5rem;
  }
  .onea-multiple-step-form .field-group h4 {
    font-size: 1rem;
    font-weight: 600;
    color: hsl(var(--foreground));
    margin-bottom: 1rem;
  }
  .onea-multiple-step-form .field-group > p {
    color: hsl(var(--muted-foreground));
    font-size: 0.875rem;
    margin-bottom: 1rem;
  }
  .onea-multiple-step-form .field-group > div > * + * {
    margin-top: 1rem;
  }
  @media (max-width: 640px) {
    .onea-multiple-step-form .field-group {
      padding: 1rem;
      margin-bottom: 1rem;
    }
    .onea-multiple-step-form .field-group h4 {
      font-size: 0.875rem;
    }
    .onea-multiple-step-form .field-group > p {
      font-size: 0.75rem;
    }
  }
  .onea-multiple-step-form input,
  .onea-multiple-step-form select,
  .onea-multiple-step-form textarea,
  .onea-multiple-step-form button[role=combobox] {
    margin-top: 0.5rem;
    background-color: white !important;
  }
  .onea-multiple-step-form div.flex.h-10.w-full.items-center.justify-between.rounded-md.border.border-input.bg-muted {
    padding: 0.5rem 0.75rem !important;
    height: 2.5rem !important;
  }
  .onea-multiple-step-form button[role=combobox] {
    padding: 0.5rem 0.75rem !important;
    height: 2.5rem !important;
  }
  .onea-multiple-step-form label {
    word-wrap: break-word;
    overflow-wrap: break-word;
    max-width: 100%;
    margin-bottom: 0.5rem;
  }
  .onea-multiple-step-form [role=radiogroup] {
    display: flex;
    flex-direction: row;
    gap: 0.25rem;
    flex-wrap: wrap;
  }
  .onea-multiple-step-form input {
    color: var(--e-global-color-text, black) !important;
  }
  .onea-multiple-step-form button[role=radio],
  .onea-multiple-step-form button[role=checkbox] {
    all: unset;
    height: 0.5rem;
    width: 0.5rem !important;
    border-radius: 100%;
    cursor: pointer;
    display: inline-flex;
    align-items: center;
    justify-content: center;
    flex-shrink: 0;
    transition: all 0.2s;
    padding: 10px !important;
    margin-top: -0.5rem !important;
    background-color: white !important;
    border-color: var(--ast-border-color) !important;
  }
  .onea-multiple-step-form button[role=radio]:hover,
  .onea-multiple-step-form button[role=checkbox]:hover {
    border: 1px solid var(--ast-global-color-0) !important;
    background-color: var(--ast-global-color-0) !important;
  }
  .onea-multiple-step-form button[role=radio]:focus-visible,
  .onea-multiple-step-form button[role=checkbox]:focus-visible {
    outline: 2px solid hsl(var(--ring));
    outline-offset: 2px;
  }
  .onea-multiple-step-form button[role=radio][data-state=checked] span,
  .onea-multiple-step-form button[role=checkbox][data-state=checked] span {
    display: flex;
    align-items: center;
    justify-content: center;
    color: var(--primary) !important;
  }
  .onea-multiple-step-form button[role=radio]:disabled,
  .onea-multiple-step-form button[role=checkbox]:disabled {
    cursor: not-allowed;
    opacity: 0.5;
  }
  .onea-multiple-step-form button[role=radio] {
    margin-right: 0.25rem !important;
  }
  .onea-multiple-step-form button[role=radio] svg {
    height: 6px;
    width: 6px;
  }
  .onea-multiple-step-form .space-y-2 > div {
    display: flex;
    align-items: flex-start;
    gap: 0.5rem;
    flex-wrap: wrap;
  }
  [data-radix-select-content] {
    z-index: 9999 !important;
    overflow-y: auto !important;
    max-height: 20rem !important;
  }
  [data-radix-select-content] .consumption-unit {
    margin-left: 0.15rem !important;
  }
  [data-radix-select-viewport] {
    padding: 0.25rem !important;
    max-height: 20rem !important;
  }
  [role=option] {
    padding: 0.5rem 0.75rem !important;
    padding-left: 2rem !important;
    cursor: pointer !important;
    position: relative !important;
    outline: none !important;
    display: flex !important;
    align-items: center !important;
  }
  [role=option][data-disabled] {
    opacity: 0.5 !important;
    pointer-events: none !important;
  }
  [role=option] > span:first-child {
    position: absolute !important;
    left: 0.5rem !important;
    display: flex !important;
    align-items: center !important;
    justify-content: center !important;
    width: 0.875rem !important;
    height: 0.875rem !important;
  }
  [role=option] > span:first-child svg {
    width: 0.875rem !important;
    height: 0.875rem !important;
    display: block !important;
  }
  [role=option] > span:last-child {
    display: inline-block !important;
  }
}